<template>
    <p>
        {{ index }}. <a :href="item.url" target="item"> {{ item.title }} </a>
        <!-- <img :src="license" /> -->
        <!-- license {{ license }} -->
        {{ item.license }} <img :src="license" />
        <!-- <img src="@/assets/cc-by.png" /> -->
    </p>

    <span v-if="item.authors.length == 1"> Author: </span>
    <span v-else-if="item.authors.length > 1"> Authors: </span>
    <span v-for="authors in item.authors" :key="authors">
        {{ authors.givenName }} {{ authors.familyName }},
    </span>
    <div>
        <span v-if="minimize">
            <span v-html="abstract_short"></span>
            <button @click="toggleAbstract">show more</button>
        </span>
        <span v-else>
            <!-- {{ item.abstract_data }} -->
            <span v-html="abstract_full"></span>
            <button @click="toggleAbstract">show less</button>
        </span>
    </div>
</template>

<script>
import license_cc_by from "../assets/cc-by.png";
import license_cc_by_sa from "../assets/cc-by-sa.png";
import license_cc_publicdomain from "../assets/cc-publicdomain.png";

export default {
    props: ["index", "item"],

    name: "ResultItem",
    // licenses: {
    //     "CC BY 2.0": license_cc_by,
    //     "CC BY-SA 4.0": license_cc_by_sa,
    // },

    data() {
        return {
            abstract_full: "",
            abstract_short: "",
            error: false,
            license: null,
            minimize: true,
            results: [],
        };
    },

    mounted() {
        this.abstract_full = this.item.abstract_data;
        this.abstract_short = this.item.abstract_data.substring(0, 300);

        if (this.item.license.includes("CC BY 2.0")) {
            this.license = license_cc_by;
        } else if (this.item.license.includes("CC BY-SA 4.0")) {
            this.license = license_cc_by_sa;
        } else if (this.item.license.includes("CC BY")) {
            this.license = license_cc_by;
        } else if (this.item.license.includes("Public Domain")) {
            this.license = license_cc_publicdomain;
        } else if (this.item.license.includes("YouTube")) {
            this.license = license_cc_by;
        } else {
            this.license = license_cc_by;
        }
    },

    // computed: {
    // license() {
    //     console.log("license = ", this.item.license);
    //     // return license_cc_by;
    //     if (this.item.license.includes("CC BY 2.0")) {
    //         return "images/cc-by.png";
    //     } else if (this.item.license.includes("CC BY-SA 4.0")) {
    //         return "images/cc-by-sa.png";
    //     } else if (this.item.license.includes("CC BY")) {
    //         return "images/cc-by.png";
    //     } else if (this.item.license.includes("YouTube")) {
    //         return "images/youtube-standard-license_0.png";
    //     }
    // },
    // short() {
    //     return this.item.abstract_data.substring(0, 300);
    // },
    // },

    methods: {
        toggleAbstract() {
            this.minimize = !this.minimize;
        },
    },
};
</script>

