<template>
    <table>
        <tr class="close">
            <td class="label">Search</td>
            <td class="spread">
                <!-- <input type="text" placeholder="Enter search word" class="search-box"> -->
                <div class="search-container">
                    <input
                        type="text"
                        placeholder=" Enter search word "
                        v-model="quick_search_string"
                        class="full"
                    />
                    <button @click="getSearchResults">Search</button>
                    <button @click="doClear">Clear</button>
                </div>
            </td>
        </tr>
        <tr class="close">
            <td></td>
            <td>
                <div class="small">
                    This searches authors, keywords, title and description
                </div>
            </td>
        </tr>
    </table>

    <div v-if="error">Error: unable to query server!</div>
    <div class="filter-search-container" v-else>
        <div class="filters-container">
            <div class="label">Filters</div>
            <hr />
            <div v-for="item in filters" :key="item">
                <FilterItem
                    :item="item"
                    :is_open="false"
                    @filter="filterUpdated"
                ></FilterItem>
            </div>
        </div>
        <div class="results-container">
            <span class="label">Search Results:</span>
            <span>{{ search_result.["hits-total"] }}</span>
            <hr />
            <div v-for="(item, index) in search_result.results" :key="item">
                <ResultItem :index="index + 1" :item="item" />
                <hr />
            </div>
        </div>
    </div>

    <!-- <div v-if="error">Error: unable to query server!</div>
  <div v-else>
    <div v-for="(value, name) in facet_names" :key="value.subject">
      facet = {{ name }} - key = {{ value }}
    </div>
    <div>
      <p>Search Results: {{ search_result.["hits-total"] }}</p>
      <div v-for="(item, index) in search_result.results" :key="item">
        <ResultItem :index="index + 1" :item="item" />
        <hr />
      </div>
    </div>
  </div> -->
</template>

<script>
import FilterItem from "./FilterItem.vue";
import ResultItem from "./ResultItem.vue";

export default {
    props: {
        msg: String,
    },

    error: "",
    name: "Search",

    components: { FilterItem, ResultItem },

    data() {
        return {
            error: false,
            facet_names: {
                keywords: ["Keywords", "show"],
                // author_org.name: ["Author Organizations", "show"],
                language_primary: ["Primary Languages", "show"],
                languages_secondary: ["Secondary Languages", "show"],
                target_audience: ["Target Audiences", "show"],
                license: ["License", "show"],
                "accessibility_features.name": [
                    "Accessibility Features",
                    "show",
                ],
                subject: ["Resource Subject", "show"],
                publisher: ["Resource Publisher", "show"],
                usage_info: ["Usage Information", "show"],
                media_type: ["Media Type", "show"],
                lr_type: ["Learning Resource Type", "show"],
                purpose: ["Resource Purpose", "show"],
                ed_frameworks: ["Educational Frameworks", "hide"],
            },
            filters: [],
            filters_selected: [],
            items_per_page: 10,
            offset: 0,
            quick_search_string: "",
            search_result: [],
            sort_str: "score desc",
        };
    },

    methods: {
        filterUpdated(data) {
            // console.log("filterUpdated!", data);
            if (data) {
                // console.log("data : ", data);
                // this.filters_selected = data;
                this.filters_selected.push(data);
            }
            this.getSearchResults();

            //this.quick_search_string = "";
        },

        getSearchResults() {
            //console.log("entered getSearchResults()");
            // console.log("quick_search_string = ", this.quick_search_string);

            this.error = false;
            // this.is_querying = true;
            let body = this.setupPostContent();
            //console.log("body: ", body);

            fetch("https://esip-dev-02.edacnm.org/api/resources/", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                    //'Access-Control-Allow-Headers': '*',
                },
                body: JSON.stringify(body),
            })
                .then((response) => {
                    if (response.ok) {
                        // DO NOT do anything to the response, including console.log
                        // or else it will cause an error that response is "disturbed"
                        return response.json();
                    } else {
                        throw new Error();
                    }
                })
                .then((result) => {
                    this.isLoading = false;
                    // console.log("result :", result);
                    this.search_result = result;
                    this.filters = this.setupFilters(result.facets);
                    // this.is_querying = false;
                    //console.log("search_result :", this.search_result);
                })
                .catch(() => {
                    this.error = true;
                });
        },

        setupFilters(result_filters) {
            // console.log("setupFilters: ", result_filters);
            // console.log("keywords : ", result_filters["keywords"]);
            //console.log("1 facet_names", this.facet_names);
            const keys = Object.keys(this.facet_names);
            // console.log("keys = ", keys);
            let filters = [];
            // let title = null;
            //let value = null;
            // console.log("filters 1", this.filters);
            keys.forEach((key) => {
                // console.log("key = ", key);
                let value = this.facet_names[key];
                // console.log("value : ", value);
                if (value[1] === "show") {
                    // console.log("show ", key);
                    if (result_filters[key]) {
                        filters.push({
                            key: key,
                            title: value[0],
                            options: result_filters[key],
                        });
                    }
                }
            });
            // console.log("Search filters : ", filters);
            return filters;
        },

        // Gather the post body information
        setupPostContent() {
            let post_content = { search: [] };
            post_content.limit = this.items_per_page;
            post_content.offset = this.offset;
            post_content.sort_str = this.sort_str;
            //console.log("xquick_search_string = ", this.quick_search_string);

            if (this.quick_search_string.length > 0) {
                post_content.search.push({
                    group: "and",
                    and: [],
                    or: [
                        {
                            field: "keywords",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "abstract_data",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "title",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "locator_data",
                            string: this.quick_search_string,
                            type: "match",
                        },
                        {
                            field: "authors.familyName",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "authors.givenName",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "target_audience",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                        {
                            field: "author_org.name",
                            string: this.quick_search_string,
                            type: "simple",
                        },
                    ],
                });
            } else {
                // console.log("in else!");
                // simple search
                post_content.search.push({
                    group: "and",
                    and: [],
                    or: [{ field: "id", string: "*", type: "simple" }],
                });
                // post_content.search.push({
                //     group: "and",
                //     and: [
                //         {
                //             field: "keywords",
                //             string: "Access rights",
                //             type: "match",
                //         },
                //     ],
                // });
            }

            // Karl's original status section
            post_content.search.push({
                group: "and",
                and: [
                    {
                        field: "status",
                        string: "true",
                        type: "simple",
                    },
                ],
            });

            // console.log("filters_selected: ", this.filters_selected);
            // const x = Object.keys(this.filter_selected);
            // console.log("length = ", this.filters_selected.length);
            // console.log("yfilters_selected: ", this.filters_selected);
            // Now add the facet selections
            // if (this.filters_selected) {
            if (this.filters_selected.length > 0) {
                // let query_array = {
                //     group: "and",
                //     and: {
                //         field: this.filters_selected.key,
                //         string: this.filters_selected.value,
                //         type: "match",
                //     },
                // };
                // console.log("query_array 2: ", query_array);
                post_content.search.push({
                    group: "and",
                    and: [
                        {
                            field: this.filters_selected[0].key,
                            string: this.filters_selected[0].value,
                            type: "match",
                        },
                    ],
                });
            }

            // console.log("post_content = ", post_content);

            return post_content;
        },
    },

    // Lifecycle functions, see
    // https://v3.vuejs.org/guide/instance.html#lifecycle-diagram for more info
    mounted() {
        // console.log("1 facet_names: ", this.facet_names);
        this.facet_names["author_org.name"] = ["Author Organizations", "show"];
        // console.log("2 facet_names: ", this.facet_names);
        this.getSearchResults();
    },

    // beforeUpdate() {
    //     console.log("beforeUpdate: ", this.is_querying);
    //     //this.setupFilters();
    // },
};
</script>

